    //         .attr('transform', k => translate(widgetBtnWidth*2 + (widgetEachWidth + widgetBtnWidth)*k.key, 3))
    //         .attr('class', k => "g"+k.key)
    //         .style('pointer-events', 'all')
    //         .on('click', function(event, k){
