import React, { useEffect, useRef, useState, useContext } from 'react'
import { DataGrid } from '@mui/x-data-grid';
import { useParams } from "react-router-dom"
import { styled } from '@mui/material/styles';

// import { UserDispatch, globalData, fieldList, colorScaleMap } from "./context/DataProvider";
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Title from './Title'
import "./TextCompare.css"
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { globalData, fieldList, colorScaleMap, lang } from './reducers/StaticData'
import { useSelector, useDispatch } from "react-redux";
import { FixedSizeList, VariableSizeList } from 'react-window';
import Divider from '@mui/material/Divider';

import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';

const Item = styled(Paper)(({ theme }) => ({
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
}));

function TextCompare() {

    const dispatch = useDispatch()
    // const { state, dispatch } = useContext(UserDispatch);

    const state = useSelector(state => state.text_compare)
    const mouse_over_state = useSelector(state => state.mouseover_to_text)
    const [option, setOption] = React.useState(1);

    const handleChange = (event, value) => {
        console.log(value)
        setOption(value);
    };

    const id_list = state.text_compare_id
    const listRef = React.createRef();
    // var heightList = []


    // console.log(option)
    // var test_data = globalData[id].ngram
    // var ngram_eng_original = test_data.ngram_eng_original
    // var ngram_eng_back = test_data.ngram_eng_back

    // var ngram_fra_original = test_data.ngram_fra_original
    // var ngram_fra_back = test_data.ngram_fra_back

    // console.log(ngram_eng_original)
    // console.log(ngram_eng_back)

    useEffect(() => {
        // console.log(mouse_over_state)
        listRef.current.scrollToItem(mouse_over_state.text_focus_id - 1, "start");
    }, [mouse_over_state])

    function createLegend() {
        return (
            <div class="legend-container" style={{ justifyContent: 'center' }}>
                <div class="legend-ngram ngram_0"> <span style={{ color: 'grey', fontSize: '1.2em' }}>Not Match </span></div>
                <div class="legend-ngram ngram_1"> <span style={{ fontSize: '1.2em' }}>Unigram </span></div>
                <div class="legend-ngram ngram_2"> <span style={{ fontSize: '1.2em' }}>Bigram </span></div>
                <div class="legend-ngram ngram_3"> <span style={{ fontSize: '1.2em' }}>3-gram </span></div>
                <div class="legend-ngram ngram_4"> <span style={{ fontSize: '1.2em' }}>4-gram </span></div>

            </div>
        )
    }

    function makeColorString(original) {
        // console.log(original)
        return (
            <div >
                {original.map(d => {
                    const subjectClass = "ngram_" + d.n_gram
                    const str = d.token
                    return <span className={subjectClass}>{str} <span className="none">&nbsp;</span></span>
                })}
            </div>
        )
    }

    function makeNormalString(original) {
        // console.log(original)
        return (<span className="ngram_0">{original}</span>)
    }

    function makeCompareView() {
        // console.log("Compare View")
        var textList = []

        var src_lang = ""
        var target_lang = ""
        if (option === 1)    // Eng <-> Back ENG
        {
            id_list.forEach(e_id => {
                var test_data = globalData[e_id].ngram
                // console.log(test_data)
                var original = makeColorString(test_data.ngram_eng_original)
                var back = makeColorString(test_data.ngram_eng_back)
                textList.push([original, back])


            })
            src_lang = "English"
            target_lang = "English Back-tranlsated"

        }
        else if (option === 2) // FRA <-> BACK FRA
        {
            id_list.forEach(e_id => {

                var test_data = globalData[e_id].ngram
                var original
                var back
				if(lang == "FRA"){
					original = makeColorString(test_data.ngram_fra_original)
					back = makeColorString(test_data.ngram_fra_back)
				}else{
					original = makeColorString(test_data.ngram_kor_original)
					back = makeColorString(test_data.ngram_kor_back)
				}
                textList.push([original, back])
            })
			src_lang = (lang == "FRA") ? "France" : "Korean"
			target_lang = (lang == "FRA") ? "France Back-translated" : "Korean Back-translated"
        }
        else {      // ENG <-> FRA
            id_list.forEach(e_id => {

                var test_data = globalData[e_id]
                var original = makeNormalString(test_data.ENG_SENTENCE)
                var back = makeNormalString((lang == "FRA") ? test_data.FRA_SENTENCE : test_data.KOR_SENTENCE)
                textList.push([original, back])
            })
            src_lang = "English"
            target_lang = (lang == "FRA") ? "France" : "Korean"

        }
        return (
            <div>
                {id_list.map((tmp_id, i) => {
                    return createCard(tmp_id, src_lang, target_lang, textList[i][0], textList[i][1])
                })}
                {/* {original.map(d => {
                    const subjectClass = "ngram_" + d.n_gram
                    const str = d.token
                    return <span className={subjectClass}>{str} <span className="ngram_0">&nbsp;</span></span>
                })} */}
            </div>
        )
    }

    function makeCompareList() {
        var textList = []

        var src_lang = ""
        var target_lang = ""
        // heightList = []

        if (option === 1)    // Eng <-> Back ENG
        {
            id_list.forEach(e_id => {
                var test_data = globalData[e_id].ngram
                // console.log(test_data)
                var original = makeColorString(test_data.ngram_eng_original)
                var back = makeColorString(test_data.ngram_eng_back)
                textList.push([original, back])
                // let tmp_height = +(((globalData[e_id].ENG_SENTENCE.length + globalData[e_id].ENG_SENTENCE.split(" ").length) / 40) * 25)
                // tmp_height+= +(((globalData[e_id].BACK_ENG_SENTENCE.length + globalData[e_id].BACK_ENG_SENTENCE.split(" ").length) / 40) * 25)
                // tmp_height += 250   // default value
                // // console.log(tmp_height)
                // heightList.push(tmp_height)

            })
            src_lang = "English Original"
            target_lang = "English tranlsated"

        }
        else if (option === 2) // FRA <-> BACK FRA
        {
            id_list.forEach(e_id => {

                var test_data = globalData[e_id].ngram
				var original = makeColorString((lang == "FRA") ? test_data.ngram_fra_original : test_data.ngram_fra_original)
                var back = makeColorString((lang == "FRA") ? test_data.ngram_fra_back : test_data.ngram_fra_back)
                textList.push([original, back])
            })
			src_lang = (lang == "FRA") ? "France Original" : "Korean Original"
			target_lang = (lang == "FRA") ? "France translated" : "Korean translated"

        }
        else {      // ENG <-> FRA

            id_list.forEach(e_id => {

                var test_data = globalData[e_id]
                var original = test_data.ENG_SENTENCE
                var back = makeNormalString((lang == "FRA")? test_data.FRA_SENTENCE : test_data.KOR_SENTENCE)
                textList.push([original, back])
            })
            src_lang = "English"
            target_lang = (lang == "FRA") ? "France" : "Korean"

        }
        return id_list.map((tmp_id, i) => {
            return createCard((i + 1), src_lang, target_lang, textList[i][0], textList[i][1])
        })


    }

    const rowList = React.useMemo(() => makeCompareList(), [state, option])
    // console.log(rowList)
    const Row = ({ index, style }) => {
        // console.log(style)
        return (

            <div style={style}>
                {rowList[index]}
            </div>
        )
    }
        ;

    const getItemSize = index => {

        // return heightList[index]
        return 500;
    };


    function createCard(id, src_language, target_language, original, back) {
        // const original_label = language + " original"
        // const back_label = language + " back-translated"
        return (
            <div style={{ margin: 20 }}>
                <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                    Rank : {id}
                </Typography>
                <Card variant="outlined">
                    <CardContent style={{padding:10, height:200}}>
                        <Typography sx={{ fontSize: '1em' }} variant="h6" component="div">
                            {src_language}
                        </Typography>
                        <Typography sx={{ fontSize: '0.8em' }}  align='left' variant="body2" paragraph={true}>
                            <br />
                            {original}
                        </Typography>

                    </CardContent>
                    <Divider />
                    <CardContent style={{padding:10, height:200}}>
                        <Typography sx={{ fontSize: '1em' }} variant="h6" component="div">
                            {target_language}
                        </Typography>
                        <Typography sx={{ fontSize: '0.8em' }} align='left' variant="body2">
                            <br />
                            {back}
                        </Typography>
                    </CardContent>
                </Card>
            </div>
        )
    }



    return (
        <div style={{ textAlign: 'center' }}>
            {/* need to replace toggle button */}
            <Title> Text Compare View</Title>
            <div>
                <ToggleButtonGroup
                    color="primary"
                    value={option}
                    exclusive
                    onChange={handleChange}
                    style={{ fontSize: "1em" }}
                >
                    <ToggleButton style={{ fontSize: "0.6em" }} value={1}>ENG</ToggleButton>
                    <ToggleButton style={{ fontSize: "0.6em" }} value={2}>{(lang == "FRA")? "FRA" : "KOR"}</ToggleButton>
                    <ToggleButton style={{ fontSize: "0.6em" }} value={3}>Original</ToggleButton>
                </ToggleButtonGroup>
            </div>

            <br />
            {createLegend()}
            <br />
            {/* {makeCompareView()} */}
            <div>
                <FixedSizeList ref={listRef}
                    height={560}
                    itemCount={rowList.length}
                    itemSize={490}

                >
                    {Row}
                </FixedSizeList>
            </div>


        </div>
    )

}

export default TextCompare