import React, { useEffect, useRef, useState, useContext } from 'react'
import * as d3 from 'd3'
import d3tip from 'd3-tip'
import { makeStyles } from "@material-ui/core/styles";
import "./css/FilterHeatmap.css"
import Title from './Title'
import { CSVLink, CSVDownload } from "react-csv";
// import { UserDispatch, globalData, fieldList, colorScaleMap } from "./context/DataProvider";

import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';


import { useSelector, useDispatch } from "react-redux";
import { globalData, fieldList, colorScaleMap, fieldList_text } from './reducers/StaticData'
import { save_lineup_filter_id } from './reducers/lineup_filter'
import { save_rev_filter_info } from './reducers/filter'

//import diff_data from './diff.json'
//import Papa from papaparse


const useStyle = makeStyles((theme) => ({
	inlineDiv: {
		display: "inline-block",
	},

	emptyDiv: {
		display: "inline-block",
		width: "30px"
	},

	outerDiv: {
		display: "inline-block",
		maxHeight: "800px",

	}

}))

export var set_scale_list = null


function Heatmap() {

	const classes = useStyle();
	const svgHeatmap1Ref = useRef()
	const svgHeatmap2Ref = useRef()

	const dispatch = useDispatch()
	const state = useSelector(state => state.filter)
	const [select_rev_id, set_select_rev_id] = useState([])
	const [remove_candidate_id, set_remove_candiate_id] = useState(null)
	const [fileData, setFileData] = useState("");
	//const lineup_filter_state = useSelector(state => state.lineup_filter)

	var rev_infos = state.rev_filter_info

	const margin = { top: 30, right: 30, bottom: 30, left: 30 }

	const [sorting, setSorting] = useState("")
	const [open, setOpen] = useState(false)
	const [heatmap1_field_list, setFieldList] = useState(fieldList)
	set_scale_list = setFieldList

	const rule_coverage_width = 40
	const filter_label_width = 70
	const remove_width = 30
	const axis_height = 50
	const axis_top_margin = 50
	const dataHeight = 20

	const handleClickOpen = () => {
		setOpen(true);
	};

	const handleClose = () => {
		setOpen(false);
	};

	const handleRemoveFilter = () => {
		setOpen(false);
		console.log("Remove candidate id : " + remove_candidate_id)
		const rev_filter_info = state.rev_filter_info.filter(d => d.id !== remove_candidate_id)

		var not_selected_data = state.not_selected_data_id
		not_selected_data = [...Array(globalData.length).keys()].map(i => i)
		rev_filter_info.forEach(__filter => {
			not_selected_data = not_selected_data.filter(d => !__filter.filter_id.includes(d))
		})
		dispatch(save_rev_filter_info({ filter: rev_filter_info, not_selected_data: not_selected_data }))
		set_select_rev_id(select_rev_id.filter(id => id !== remove_candidate_id))
		dispatch(save_lineup_filter_id(select_rev_id.filter(id => id !== remove_candidate_id)))
	}

	useEffect(() => {
		console.log("Heatmap use effect")
		d3.select(svgHeatmap1Ref.current).selectAll("*").remove()

		const createChart = (tmp_svg, data, scale_list, width, height) => {

			let dataLen = data.length;
			let dimLen = scale_list.length // -1 means excluding id

			const scaleHeight = 30;



			const margin = { top: 30, right: 30, bottom: 30, left: 30 }
			let svg = d3.select(tmp_svg.current)
			svg.attr("width", width).attr("height", height)


			// weightWidth = totalWidth - each margin - flter_label_width - each margin - numbre_width

			const weightWidth = width - 10 - rule_coverage_width - filter_label_width - 10 - remove_width

			let dimWidth = weightWidth / dimLen;

			const colorScale = d3.scaleLinear().domain([0, 1]).range([0, 1])
			var axisX = d3.scaleBand().range([0, weightWidth]).domain(scale_list.map(d => fieldList_text[d])).padding(0.01)
			var axis_rule_coverage = d3.scaleBand().range([0, filter_label_width]).domain(["# of item"]).padding(0.01)
			var axis_filter_label = d3.scaleBand().range([0, filter_label_width]).domain(["Filter Label"]).padding(0.01)

			// Create Label

			// Filter Coverage
			svg.append("g").attr("class", "xaxis axis").attr("transform", `translate(0, ${axis_height - 5})`).call(d3.axisTop(axis_filter_label).tickSize(0)).call(g => g.select(".domain").remove())

			// Rule Coverage
			svg.append("g").attr("class", "xaxis axis").attr("transform", `translate(${filter_label_width + 10}, ${axis_height - 5})`).call(d3.axisTop(axis_rule_coverage).tickSize(0)).call(g => g.select(".domain").remove())

			// Filter Label
			const filter_label_svg = svg.append("g").attr("class", "xaxis axis").attr("transform", `translate(${rule_coverage_width + 10 + filter_label_width + 30}, ${axis_height})`).call(d3.axisTop(axisX).tickSize(0)).call(g => g.select(".domain").remove())

			// const axisSvg = svg.append("g").attr("class","xaxis axis").attr("transform", `translate(${rule_coverage_width + 10 + filter_label_width+10}, ${axis_height - 5})`).call(d3.axisTop(axisX).tickSize(0)).call(g => g.select(".domain").remove())


			svg.selectAll(".xaxis text").attr("transform", function (d) {
				return "rotate(-70)";
			})

			var newSvg = svg.append("g").attr("transform", `translate(0,${axis_height + axis_top_margin})`)

			// svg.append("rect").attr("width", filter_label_width).attr("height",dataHeight).attr(fill, data["rev_color"])
			// svg = svg.append("g").attr("transform", `translate(0, ${filter_label_width + 10})`)
			var groups = newSvg
				.selectAll("g.row")
				.data(data, (d) => {
					return d.id
				})
				.join("g")
				.attr("transform", (d, i) => {
					const y_pos = Math.trunc(i);
					const pos = i * dataHeight;
					return `translate(0,${pos})`
				}
				)
			var body_group = groups.append("g")
				.style("cursor", "pointer")
				.on("mouseover", (event, d) => {
					const row_id = "#row_" + d.id
					if (!select_rev_id.includes(d.id))
						svg.selectAll(row_id).style("stroke", "lightgrey").style("stroke-width", 4)
				}).on("mouseout", function (event, d) {
					if (!select_rev_id.includes(d.id)) {
						const row_id = "#row_" + d.id
						svg.selectAll(row_id).style("stroke", "none").style("stroke-width", 0)
					}

				}).on("click", (event, d) => {
					// console.log("SELECTE REV ID : " + select_rev_id)
					// console.log("d.id : " + d.id)
					if (select_rev_id.includes(d.id)) {
						// console.log("SAME ID")
						set_select_rev_id(select_rev_id.filter(id => id !== d.id))
						dispatch(save_lineup_filter_id(select_rev_id.filter(id => id !== d.id)))

					}
					else {
						// console.log("HERE")
						const row_id = "#row_" + d.id
						let select_id = [].concat(select_rev_id);
						select_id.push(d.id);
						console.log(select_id)
						set_select_rev_id(select_id)
						dispatch(save_lineup_filter_id(select_id))
						svg.selectAll(row_id).style("stroke", "red").style("stroke-width", 4)
					}
				})


			const label_group = body_group.append("g").attr("transform", (d, i) => {
				return `translate(0 , 0)`
			})

			label_group.append("rect").attr("width", filter_label_width)
				.attr("height", dataHeight).attr("fill", d => {
					return d.rev_color
				})



			label_group.append("text").text((d, i) => { return d.name })
				.attr("x", (d, i) => { return 5 }).attr("y", (d, i) => { return dataHeight / 1.5 }).attr("font-size", "0.8em").attr("fill", "white")

			// Rule coverage 


			const rule_coverage_svg = body_group.append("g").attr("transform", (d, i) => {
				return `translate(${filter_label_width + 10},0)`
			})


			// Rule coverage  
			// Rule Coverage First Width : rule_coverage_widt h - d.filter_id.length
			// Rule Coverage rest of them
			const total_data_length = globalData.length
			const rule_coverage_scale = d3.scaleLinear().range([0, rule_coverage_width]).domain([0, total_data_length])

			rule_coverage_svg.append("rect").attr("id", (d, i) => "rule_coverage_" + i).attr("width", rule_coverage_width).attr("height", dataHeight).style("fill", "none").style("stroke", "grey").style("stroke-width", 1)
			rule_coverage_svg.append("rect").attr("width", (d, i) => {
				return rule_coverage_scale(d.filter_id.length)
			}).style("fill", "gray")


				.attr("height", dataHeight).attr("fill", d => {
					return d.rev_color
				})


			rule_coverage_svg.append("text").text((d, i) => { return d.filter_id.length })
				.attr("x", (d, i) => { return rule_coverage_width / 4 }).attr("y", (d, i) => { return dataHeight / 1.5 }).attr("font-size", "0.8em").attr("fill", "black")






			const weight_group = body_group.append("g").attr("transform", (d, i) => {
				return `translate(${rule_coverage_width + 10 + filter_label_width + 10},0)`
			})

			d3.selectAll(".tip-info").remove()
			var tool_tip = d3tip()
			tool_tip.attr("class", "d3-tip tip-info").html(function (e, d) {
				tool_tip.style("margin-left", (parseFloat((d3.select(".tip-info").style("left"))) - 960) / 4 + "px")
				tool_tip.style("margin-top", parseFloat((d3.select(".tip-info").style("top"))) / 4 + "px")
				return d
			})
			svg.call(tool_tip)

			weight_group.selectAll("rect").data((d, i) => {
				// console.log(d)
				// console.log(d.val)
				const weightList = heatmap1_field_list.map(field => d['weight'][field])
				return weightList
			}).join("rect").attr("x", (d, i) => {
				//console.log(i)
				//const key = mapping[i]
				const x_pos = i % dimLen
				return x_pos * dimWidth
			}).attr("y", (d, i) => {
				const y_pos = Math.trunc(i / dimLen);
				return y_pos * dataHeight;
			})
				.attr("width", dimWidth)
				.attr('height', dataHeight).attr('stroke', 'gray')
				.attr("fill", (d, i) => {
					return d3.interpolateGreens(colorScale(d))
				})
				.on("mouseover", tool_tip.show)
				.on("mouseout", tool_tip.hide)


			const remove_group = groups.append("g").attr("transform", (d, i) => {
				return `translate(${width - remove_width},0)`
			})
			const x_button = remove_group.append("image").attr("href", "X.svg").attr("x", 8).attr("y", 3).attr("height", 14).attr("width", 14).style("cursor", "pointer")
			x_button.on("click", (event, d) => {
				set_remove_candiate_id(d.id)
				setOpen(true)
			})
				.on("mouseover", (event, d) => {
					d3.select(event.target).style("outline", "auto").style("outline-color", "silver")
				})
				.on("mouseout", (event, d) => {
					d3.select(event.target).style("outline", "none")
				})

			const row = groups.append("rect").attr("id", (d, i) => "row_" + d.id)
				.attr("width", width - remove_width).attr("height", dataHeight).attr("fill", 'none')
				.style("stroke", d => select_rev_id.includes(d.id) ? "red" : "none").style("stroke-width", 4)
		}

		const rev_infos = state.rev_filter_info
		createChart(svgHeatmap1Ref, rev_infos, heatmap1_field_list, 270, 260)

		var english = state.not_selected_data_id.map(d => {
			return globalData[d].ENG_SENTENCE
		})
		var france = state.not_selected_data_id.map(d => {
			return globalData[d].FRA_SENTENCE
		})

		setFileData(jsonToTSV(english, france))
	}, [state, select_rev_id, remove_candidate_id, heatmap1_field_list])

	function jsonToTSV(english, france) {
		let tsv_string = '';
		for (var i = 0; i < english.length; i++) {
			let row = `${english[i]}\t${france[i]}`
			tsv_string += (tsv_string === '' ? `${row}` : `\n${row}`);

		}
		return tsv_string;
	}

	return (
		<div className={classes.outerDiv}>
			<svg ref={svgHeatmap1Ref}></svg>

			{/* <div className={classes.emptyDiv}></div>
      <div className={classes.inlineDiv}>
        <h2> cosine similarity </h2>

        <svg ref={svgHeatmap2Ref}></svg>
      </div> */}


			<Dialog
				open={open}
				onClose={handleClose}
				// aria-labelledby="alert-dialog-title"
				// aria-describedby="alert-dialog-description"
			>
				<DialogTitle id="alert-dialog-title">
					{"Delete Confirmation?"}
				</DialogTitle>
				<DialogContent>
					<DialogContentText id="alert-dialog-description">
						Are you sure to delete this filter?
          			</DialogContentText>
				</DialogContent>
				<DialogActions>
					<Button onClick={handleClose}>Cancle</Button>
					<Button onClick={handleRemoveFilter} >
						Okay
					</Button>
				</DialogActions>
			</Dialog>
			<CSVLink data={fileData} filename="results.tsv" noHeader={true}>
				<Button style={{ color: "black" }} color="inherit" variant="contained" size="small">Save Data to .csv</Button>
			</CSVLink>
		</div>
	);
}



export default Heatmap;
