import { useEffect, useState, useRef } from "react";
import * as d3 from "d3"
import { numberStatsGenerator, FilterRangeHistogram, FilterRangeBoxPlot } from '@lineup-lite/components';
import { globalData, fieldList, fieldList_text, colorScaleMap } from "./reducers/StaticData";
import { useSelector, useDispatch } from "react-redux";

var x;
var y;

export var set_field_boxplot = null

function FilterBoxPlot() {

    const compute = numberStatsGenerator();
    
    const state = useSelector(state => state.filter)

	const [myField, setMyField] = useState(fieldList)

	set_field_boxplot = setMyField

    const boxplot_ref = useRef()


    useEffect(() => {
		
        //    backend     ..
        const stat = fieldList.map(field => {
            return {key : field, value : compute(globalData.map(d => d[field]))}
        })
        var y_max = 0
        var y_min = 999999
        stat.forEach(tmp_stat => {
            if(y_max < tmp_stat.value.max) 
                y_max = parseFloat(tmp_stat.value.max)
            if(y_min > tmp_stat.value.min)
                y_min = parseFloat(tmp_stat.value.min)
        })

        var margin = { top: 10, right: 30, bottom: 120, left: 30 },
            width = 280 - margin.left - margin.right,
            height = 310 - margin.top - margin.bottom;

        const space = 17

        d3.select(boxplot_ref.current).selectAll("*").remove()

        // append the svg object to the body of the page
        var svg = d3.select(boxplot_ref.current)
            .attr("width", width + margin.left + margin.right)
            .attr("height", height + margin.top + margin.bottom)
            .append("g")
            .attr("id","filter_boxplot")
            .attr("transform",
                "translate(" + margin.left + "," + margin.top + ")");

        // Read the data and compute summary statistics for each specie

        // const myData = [{ key: 'CORR', value : {q1 : 0.5, median : 0.7, q3 : 0.9, interQuantileRange : 0.4, min : 0.5 - 1.5 * 0.4, max : 0.9 + 1.5 * 0.4}}
        //     ,{ key : 'BACK_ENG_CORR', value: {q1 : 0.6, median : 0.73, q3 : 0.8, interQuantileRange : 0.2, min : 0.6 - 1.5 * 0.2, max : 0.8 + 1.5 * 0.2}}
        //     ,{ key : 'BACK_FRA_CORR', value : {q1 : 0.3, median : 0.5, q3 : 0.7, interQuantileRange : 0.4, min : 0.3 - 1.5 * 0.4, max : 0.7 + 1.5 * 0.4}}]

        // // Compute quartiles, median, inter quantile range min and max --> these info are then used to draw the box.
        // var sumstat = d3. // nest function allows to group the calculation per level of a factor
        //     rollup(data, d => d.id, function (d) {
        //         var q1 = d3.quantile(d.map(function (g) { return g.CORR; }).sort(d3.ascending), .25)
        //         var median = d3.quantile(d.map(function (g) { return g.CORR; }).sort(d3.ascending), .5)
        //         var q3 = d3.quantile(d.map(function (g) { return g.CORR; }).sort(d3.ascending), .75)
        //         var interQuantileRange = q3 - q1
        //         var min = q1 - 1.5 * interQuantileRange
        //         var max = q3 + 1.5 * interQuantileRange
        //         return ({ q1: q1, median: median, q3: q3, interQuantileRange: interQuantileRange, min: min, max: max })
        //     })
        //     .entries(data)


        // Show the Y scale



        x = d3.scaleBand()
            .range([0, width])
            .domain(myField)
            .paddingInner(1)
            .paddingOuter(.5)
        svg.append("g")
            .attr("transform", "translate(0," + height + ")")
            .call(d3.axisBottom(x).tickFormat(d => fieldList_text[d]))
			.selectAll("text")
			.style("text-anchor", "end")
			.attr("dx", "-.8em")
			.attr("dy", ".15em")
			.attr("transform", function (d) {
				return "rotate(-70)";
			  })


        // Show the Y scale
        y = d3.scaleLinear()
            .domain([y_min, y_max])
            .range([height, 0])
        svg.append("g").call(d3.axisLeft(y))

        // Show the main vertical line

        const boxplot_svg = svg.append("g")

        boxplot_svg
            .selectAll("vertLines")
            .data(stat)
            .enter()
            .append("line")
            .attr("x1", function (d) { return (x(d.key)) })
            .attr("x2", function (d) { return (x(d.key)) })
            .attr("y1", function (d) { return (y(d.value.min)) })
            .attr("y2", function (d) { return (y(d.value.max)) })
            .attr("stroke", "lightgrey")
            .style("stroke-dasharray",("10","3"))
            .style("width", 40)

        // rectangle for the main box
        var boxWidth = (width / myField.length) - space
        boxplot_svg
            .selectAll("boxes")
            .data(stat)
            .enter()
            .append("rect")
            .attr("x", function (d) { return (x(d.key) - boxWidth / 2) })
            .attr("y", function (d) { return (y(d.value.q3)) })
            .attr("height", function (d) { return (y(d.value.q1) - y(d.value.q3)) })
            .attr("width", boxWidth)
            .attr("stroke", "black")
            .style("fill", d => colorScaleMap[d.key](0.5))
            .style("opacity",0.8)

        // Show the median
        boxplot_svg
            .selectAll("medianLines")
            .data(stat)
            .enter()
            .append("line")
            .attr("x1", function (d) { return (x(d.key) - boxWidth / 2) })
            .attr("x2", function (d) { return (x(d.key) + boxWidth / 2) })
            .attr("y1", function (d) { return (y(d.value.median)) })
            .attr("y2", function (d) { return (y(d.value.median)) })
            .attr("stroke", "black")
            .style("width", 80)

        
        

    }, [myField])


    useEffect( () => {
        const filter_info = state.rev_filter_info

        d3.select(boxplot_ref.current).
        
        selectAll("#path_g").remove()
        const svg = d3.select("#filter_boxplot")
        // svg.selectAll("*").remove()
        // Draw the line

        var avg_list = []
        filter_info.forEach(cur_info => {
            const id = cur_info.id
            const rev_color = cur_info.rev_color
            const filter_id = cur_info.filter_id

            var tmp_avg_dict = {}

            fieldList.forEach(field => {
                tmp_avg_dict[field] = 0
            })


            filter_id.map(tmp_id => {
                fieldList.forEach(field => {
                    tmp_avg_dict[field] += parseFloat(globalData[tmp_id][field])
                })
            })
            const avg_info = myField.map(d => {
                return {field : d, avg : (tmp_avg_dict[d] / filter_id.length)}
            })
    
            avg_list.push({id : id, rev_color : rev_color, avg : avg_info})
        })
        
        // console.log(avg_list)


        var valueline = d3.line().x(d => x(d.field)).y(d => y(d.avg))
        const path_svg = svg.selectAll("filter_path").data(avg_list, d=> d.id).enter().append("g").attr("id","path_g")
        path_svg.selectAll("my_path").data(d => [d.avg]).enter().append("path").attr("fill", "none").attr("stroke",
        function(d) {
            return d3.select(this.parentNode).datum().rev_color      
        })
        .attr("stroke-width",1.5).attr("d",valueline)


    },[state, myField])
    // set the dimensions and margins of the graph


    return (
        <div id="my_dataviz">
            <svg ref={boxplot_ref} />
        </div>
    )
}

export default FilterBoxPlot;