

const initalState = {
    text_focus_id : 0,
  };
  
  export const TEXT_FOCUS_ID = "SET_TEXT_FOCUS_ID"
 
  export const save_text_focus_id = data => ({ type: TEXT_FOCUS_ID, data })

  
  const mouseover_to_text = (state = initalState, action) => {
    switch (action.type) {
        case "SET_TEXT_FOCUS_ID" :
            return {
              ...state,
              text_focus_id : action.data
        }
      // default    state count undefined  ! default 
      default:
        return state;
    }
  };
  
  export default mouseover_to_text