import React, { useRef, useEffect, useContext, useState } from "react";
import * as d3 from "d3";

import { useSelector, useDispatch } from "react-redux";
// import { increseCount } from "./reducers/";
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import { globalData, fieldList, colorScaleMap, fieldList_text } from './reducers/StaticData'
import FormHelperText from '@mui/material/FormHelperText';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';

// import { UserDispatch, globalData, fieldList, colorScaleMap } from "./context/DataProvider";

const CanvasScatter = () => {



    const [x_option, set_x_option] = useState(fieldList[1])
    const [y_option, set_y_option] = useState(fieldList[2])




    const dispatch = useDispatch();
    const state = useSelector(state => state.filter);

    const splotSvg = useRef(null);
    const divRef = useRef(null);

    const data = globalData
    const isSavedFiltered = state.isSavedFiltered



    const width = 240
	const height = 200
    const margin = { left: 80, right: 20, top: 20, down: 50 }


    const divwidth = width + margin.left + margin.right
    const divheight = height + margin.top + margin.down


    let x_min = d3.min(data, d => +(d[x_option]));
    let x_max = d3.max(data, d => +(d[x_option]));
    let y_min = d3.min(data, d => +(d[y_option]));
    let y_max = d3.max(data, d => +(d[y_option]));

    var x = d3.scaleLinear().domain([x_min, x_max]).range([0, width]) // [45, 425]
    var y = d3.scaleLinear().domain([y_min, y_max]).range([height, 0])  // [330,]
    const radius = 1

    const createMenuItem = () => {
        const menu_item_list = fieldList.map(field => {
            return (
                <MenuItem style={{fontSize:"0.6em"}} value={field}>{fieldList_text[field]}</MenuItem>
            )
        })
        console.log(menu_item_list)
        return menu_item_list
    }

    const handle_x_change = (event) => {
        set_x_option(event.target.value);
    };

    const handle_y_change = (event) => {
        set_y_option(event.target.value);
    };


    useEffect(() => {
        // TODO
        //        console.log("USE Effect")

        d3.select(splotSvg.current).selectAll("*").remove();
        d3.select(divRef.current).selectAll("*").remove()





        var axis_x = d3.axisBottom(x);
        var axis_y = d3.axisLeft(y);

        let svg = d3.select(splotSvg.current)

        svg.append("g")
            .attr("id", "axis_x")
            .attr("transform", "translate(" + margin.left + "," + (height + margin.top) + ")")
            .call(axis_x);
        svg.append("g")
            .attr("id", "axis_y")
            .attr("transform", "translate(" + margin.left + "," + margin.top + ")")
            .call(axis_y);



        svg.append('text')
            .attr('x', `-${divwidth / 2}`)
            .attr('y', `${margin.left / 2}`)
            .attr('transform', 'rotate(-90)')
            .text(fieldList_text[x_option])
            .attr("font-size","smaller")
        svg.append('text')
            .attr('x', `${divwidth / 2}`)
            .attr('y', `${divheight - margin.down / 4 }`)
            .text(fieldList_text[y_option])
            .attr("font-size","smaller")


        svg = svg.append("g").attr("id", d => "scatter_plot").attr("transform", "translate(" + margin.left + ", " + margin.top + ")")

        var myCanvas = d3.select(divRef.current).append("canvas").attr("width", width).attr("height", height)
            .style('margin-left', margin.left + 'px')
            .style('margin-top', margin.top + 'px')

        const context = myCanvas.node().getContext('2d');

        data.forEach(d => {
            const point1 = d[x_option]
            const point2 = d[y_option]
            drawPoint([point1, point2])
        })

        function drawPoint(point) {
            context.beginPath();
            context.globalAlpha = 0.5

            context.fillStyle = "lightgrey";
            const px = x(point[0]);
            const py = y(point[1]);

            context.arc(px, py, 1.2, 0, 2 * Math.PI, true);
            context.fill();
        }




    }, [x_option, y_option]);

    useEffect(() => {
        // console.log("isSavedFilter")
        const mySvg = d3.select("#scatter_plot")
        // const rowList = []
        // state..forEach(idx => {
        //     rowList.push(globalData[idx])
        // })
        const filter_info = state.rev_filter_info

        const groups = mySvg.selectAll("g").data(filter_info, d => d.id).join("g")
        groups.selectAll("circle").data(d => {
            return d.filter_id.map(d => globalData[d])
        }).join("circle").attr("r", radius + 1)
            .attr("cx", d => x(d[x_option])).attr("cy", d => y(d[y_option])).attr("fill", function (d, i) {
                const pData = d3.select(this.parentNode).datum()
                // console.log(pData.rev_color)
                return pData.rev_color
            })
            .attr("stroke-width", d => 1)

    }, [state, x_option, y_option])

    return (
        <div>
            <div style={{textAlign:"center"}}>
                <div style={{ display: "inline-block" }}>
                    <FormControl sx={{ m: 1, minWidth: 120 }}>
                        <InputLabel id="x_axis_label">x_axis</InputLabel>

                        <Select
                            labelId="x-option-select-label"
                            id="x-option-select"
                            value={x_option}
                            label="Language"
                            onChange={handle_x_change}
                            style={{fontSize:"0.6em"}}
                        >
                            {createMenuItem()}

                        </Select>

                    </FormControl>
                </div>
                <div style={{ display: "inline-block" }}>
                    <FormControl sx={{ m: 1, minWidth: 120 }}>
                        <InputLabel id="y_axis_label">y_axis</InputLabel>

                        <Select
                            labelId="y-option-select-label"
                            id="y-option-select"
                            value={y_option}
                            label="Language"
                            onChange={handle_y_change}
                            style={{fontSize:"0.6em"}}
                        >
                            {createMenuItem()}

                        </Select>
                    </FormControl>

                </div>
            </div>

            <div style={{ width: divwidth, height: divheight, position:"relative" }} >
                <div ref={divRef} style = {{position: "absolute"}}></div>
                <svg ref={splotSvg} width={divwidth} height={divheight} style={{ position: "absolute", top : 0, left : 0 }}>
                </svg>

            </div>
        </div>
    )
};

export default CanvasScatter;