import React, { useEffect, useRef, useState, useContext } from 'react'
import { DataGrid } from '@mui/x-data-grid';
import { useParams } from "react-router-dom"
import { styled } from '@mui/material/styles';

import { UserDispatch, globalData } from "./context/DataProvider";
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Title from './Title'



const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));


const columns = [
  { field: 'id', headerName: 'ID' },
  { field: 'CORR', headerName: 'CORR', width: 200 },
  { field: 'BACK_ENG_CORR', headerName: 'BACK_ENG_CORR', width: 200 },
  { field: 'BACK_FRA_CORR', headerName: 'BACK_FRA_CORR', width: 200 },
  { field: 'ENG_BLEU', headerName: 'ENG_BLEU', width: 200 },
  { field: 'FRA_BLEU', headerName: 'FRA_BLEU', width: 200 },
  { field: 'ENG_METEOR', headerName: 'ENG_METEOR', width: 200 },
  { field: 'FRA_METEOR', headerName: 'FRA_METEOR', width: 200 },

];

const rows = [
  { id: 1, lastName: 'Snow', firstName: 'Jon', age: 35 },
  { id: 2, lastName: 'Lannister', firstName: 'Cersei', age: 42 },
  { id: 3, lastName: 'Lannister', firstName: 'Jaime', age: 45 },
  { id: 4, lastName: 'Stark', firstName: 'Arya', age: 16 },
  { id: 5, lastName: 'Targaryen', firstName: 'Daenerys', age: null },
  { id: 6, lastName: 'Melisandre', firstName: null, age: 150 },
  { id: 7, lastName: 'Clifford', firstName: 'Ferrara', age: 44 },
  { id: 8, lastName: 'Frances', firstName: 'Rossini', age: 36 },
  { id: 9, lastName: 'Roxie', firstName: 'Harvey', age: 65 },
];

export default function DataTable() {

  const { id } = useParams();

  const newSet = new Set(id.split(","))
  //console.log(newSet)
  const rows = globalData.filter(d => {
    return newSet.has('' + d.id)
  })

  const [eng, set_eng] = useState("")
  const [back_eng, set_back_eng] = useState("")
  const [fra, set_fra] = useState("")
  const [back_fra, set_back_fra] = useState("")


  // console.log(rows)
  return (
    <div style={{ height: 600, width: '80%' , margin : 'auto'}}>
      <DataGrid
        rows={rows}
        columns={columns}
        pageSize={100}
        rowsPerPageOptions={[50]}
        checkboxSelection
        onRowClick={(d) => {
          set_eng(globalData[d.id].ENG_SENTENCE)
          set_back_eng(globalData[d.id].BACK_ENG_SENTENCE)
          set_fra(globalData[d.id].FRA_SENTENCE)
          set_back_fra(globalData[d.id].BACK_FRA_SENTENCE)
          // set
          // console.log(d)
          // console.log(globalData[d.id].ENG_SENTENCE)
          // console.log(globalData[d.id].BACK_ENG_SENTENCE)
        }

        }
      />
      <br />
      <br />
      <Grid container item spacing={3}>
        <Grid container item spacing={2}>
          <Grid item xs={6}>
            <Title> English </Title>
            <Item>
              {eng}
            </Item>
          </Grid>
          <Grid item xs={6}>
            <Title> Back-English </Title>
            <Item>
            {back_eng}
            </Item>
          </Grid>
        </Grid>

        <Grid container item spacing={2}>
          <Grid item xs={6}>
            <Title> Freench </Title>
            <Item>
              {fra}
            </Item>
          </Grid>
          <Grid item xs={6}>
            <Title> Back-French </Title>
            <Item>
            {back_fra}
            </Item>
          </Grid>
        </Grid>
      </Grid>


    </div>
  );
}
