// import original_data from "../extractor/kor_eng_10000.json"
import original_fra_data from "../extractor/taeri_rev.json"
import original_kor_data from "../extractor/taeri_rev.json"
// import original_data from "../extractor/50000_full_ngram.json"
import * as d3 from 'd3'
//import ngram_data from "../extractor/ngram.json"

//export let lang = window.location.pathname.slice(1);
export let lang = 'KOR'

// invert color scale

const color_strength = 1.3


 
const interpolateBlues = idx => d3.interpolateBlues(1 - +(idx/color_strength))   
const interpolateGreens = idx => d3.interpolateGreens(1 - +(idx/color_strength))   
const interpolateGreys = idx => d3.interpolateGreys(1 - +(idx/color_strength))   
const interpolateOranges = idx => d3.interpolateOranges(1 - +(idx/color_strength))   
const interpolatePurples = idx => d3.interpolatePurples(1 - +(idx/color_strength))   
const interpolateReds = idx => d3.interpolateReds(1 - +(idx/color_strength))
const brown_pink_scale = d3.scaleLinear().domain([0,1]).range([0.0,0.48])
const interpolateBrown = idx => d3.interpolateBrBG(brown_pink_scale(idx/color_strength))
const interpolatePink = idx => d3.interpolatePiYG(brown_pink_scale(idx/color_strength))

const bg_scale = d3.scaleLinear().domain([0,1]).range([1,0.52])
const interpolateBrownGreen = idx => d3.interpolateBrBG(bg_scale(idx/ color_strength))

export const colorScaleList = [interpolateBlues, interpolateGreens, interpolateGreys, interpolateOranges, interpolatePurples
, interpolateReds, interpolateBrown, interpolatePink, interpolateBrownGreen, interpolateOranges, interpolatePurples]


export const fixed_lineup_list = (lang == "FRA") ? ['expand', 'rank', 'selection', "ENG_SENTENCE", "FRA_SENTENCE", "Weighted Sum"]:['expand', 'rank', 'selection', "ENG_SENTENCE", "KOR_SENTENCE", "Weighted Sum"]

// export const fieldList = ["CORR", "BACK_ENG_CORR", "BACK_FRA_CORR", "LENGTH_DIFF", "TOKEN_LENGTH_DIFF"] 
export const fieldList = (lang == "FRA") ? ["CORR", "BACK_ENG_CORR", "BACK_FRA_CORR", "ENG_BLEU", "FRA_BLEU", "ENG_METEOR", "FRA_METEOR", "LENGTH_DIFF", "TOKEN_LENGTH_DIFF"] 
								:["CORR", "BACK_ENG_CORR", "BACK_KOR_CORR", "ENG_BLEU", "KOR_BLEU", "ENG_METEOR", "KOR_METEOR", "LENGTH_DIFF", "TOKEN_LENGTH_DIFF"] 


export const fieldList_text = {
	"CORR" : "Cosine Sim.",
	"BACK_ENG_CORR" : "ENG Cosine Sim.",
	"BACK_FRA_CORR" : "FRA Cosine Sim.",
	"BACK_KOR_CORR" : "KOR Cosine Sim.",
	"LENGTH_DIFF" : "Length Ratio(R)",
	"TOKEN_LENGTH_DIFF" : "Token Length Ratio(R)",
	"ENG_BLEU" : "ENG BLEU",
	"FRA_BLEU" : "FRA BLEU",
	"KOR_BLEU" : "KOR BLEU",
	"ENG_METEOR" : "ENG METEOR",
	"FRA_METEOR" : "FRA METEOR",
	"KOR_METEOR" : "KOR METEOR"
}

export var colorScaleMap = {}
fieldList.forEach((field,i) => {
  colorScaleMap[field] = colorScaleList[i]
})
var original = (lang == "FRA") ? original_fra_data : original_kor_data
export var max_LENGTH_DIFF = d3.max(original, d => d["LENGTH_DIFF"]);
export var max_TOKEN_LENGTH_DIFF = d3.max(original, d => d["TOKEN_LENGTH_DIFF"]);

export var globalData = original.map(d => {
	d["LENGTH_DIFF"] = 1 - d["LENGTH_DIFF"] / max_LENGTH_DIFF ;
	d["TOKEN_LENGTH_DIFF"] = 1 - d["TOKEN_LENGTH_DIFF"] / max_TOKEN_LENGTH_DIFF;
	return d;
})
export const all_id_list = Array(globalData.length).fill().map((d,i) => i)
