import React, { useContext, useEffect, useState } from 'react';
import { useFilters, useTable, useGroupBy, useExpanded, useRowSelect, useSortBy, useResizeColumns, useBlockLayout, useColumnOrder } from 'react-table';
import { useRowExpandColumn, useSortingOptions, useRowRankColumn, useRowSelectColumn, useStats, asTextColumn } from '@lineup-lite/hooks';
import { TextSummaryRenderer, CategoricalHistogramRenderer, NumberHistogramRenderer } from '@lineup-lite/hooks';
import { FixedSizeList } from 'react-window'
import LineUpLite, {
  asNumberColumn,
  featureRowRank,
  asStackedNumberColumn,
  featureFlexLayout,
  asCategoricalColumn,
  LineUpLiteColumn,
  LineUpLiteComponentLike
} from '@lineup-lite/table'
import '@lineup-lite/table/dist/table.css';
import "./css/MyLineup.css"
import { defaultColorScale, defaultConstantColorScale, defaultConstantDarkColorScale, defaultCategoricalColorScale } from '@lineup-lite/components';
// import { UserDispatch, globalData, fieldList, colorScaleMap } from "./context/DataProvider";
import Button from '@mui/material/Button';
//import Slider from '@mui/material/Slider';
import FlipMove from 'react-flip-move';

import WeightSlider from './WeightSlider'
import { styled } from '@mui/material/styles';
import ScrollBarWidth from './ScrollBarWidth'
import Paper from '@mui/material/Paper';
import * as d3 from 'd3'
import { useSelector, useDispatch } from "react-redux";

import { globalData, fieldList, colorScaleMap, colorScaleList, all_id_list, lang, fieldList_text } from './reducers/StaticData'
import { save_text_compare_id } from './reducers/text_compare'
import { save_rev_filter_info } from './reducers/filter'
import { save_column_order } from './reducers/php_lineup'
import { save_mouseover_id } from './reducers/lineup_to_php'
import { save_text_focus_id } from './reducers/mouseover_to_text'
import Title from './Title'
import Modal from '@mui/material/Modal';

//import ColorPicker from './ColorPicker'
import { CirclePicker } from 'react-color';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 350,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

export var set_column_function = null

const BodyWrapper = ({
  children,
  style,
  ref,
  ...rest
}) => {
  return (
    <FlipMove enterAnimation="fade" leaveAnimation="fade" {...rest} ref={ref} style={style}>
      {children}
    </FlipMove>
  );
};

let filter_name = "Label1"
let filter_num = 1;
function MyLineup() {
  console.log("lineup")

  const dispatch = useDispatch()
  // const { state, dispatch } = useContext(UserDispatch);

  const state = useSelector(state => state.weight)
  const filter_state = useSelector(state => state.filter)
  const lineup_filter_state = useSelector(state => state.lineup_filter)

  const php_lineup_state = useSelector(state => state.php_lineup)
  const [select_color, set_select_color] = useState("#f44336")
  const components = React.useMemo(
    () => ({
      tbody: BodyWrapper,
    }),
    []
  );

  const data = React.useMemo(() => globalData,
    [state]
  );

  var field_list_final = fieldList

  // field_list_final.push.apply(field_list_final, heatmap1_field_list)
  // field_list_final.push.apply(field_list_final, heatmap2_field_list)
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  
  

  const handleColorChange = (color, event) => {
    console.log(color.hex)

    set_select_color(color.hex)
  }

  // var {currentWeight} = useSelector(state =>state.weight)
  var currentWeight = state.weight
  // console.log(currentWeight)
  //const currentWeight = state.weight
  const mycolor = ["blue","green","grey","orange","purple","red","lightgrey","white","black"]
  const myColumn =
    field_list_final.map((d, i) => {
      return { col: d, weight: currentWeight[d], color: colorScaleMap[d](0.5) }
    }
    )

    
  // const myColumn = change === true ? field_list_final.map((d, i) => {
  //   return { col: d, weight: (i + 1), color: defaultConstantColorScale }
  // })
  //   : field_list_final.map((d, i) => {
  //     return { col: d, weight: (1), color: defaultConstantColorScale
  //     }
  //   })

  const scrollBarSize = React.useMemo(() => ScrollBarWidth(), [])


  const custom_maxWidth = 80

  const stackNumColumn = React.useMemo(() => asStackedNumberColumn({ Header: 'Weighted Sum', width: 110, maxWidth: custom_maxWidth }, myColumn,
    {
      min: 0
    })
    , [state])


  function showTextLabel(v) {
    return v
  }
  const col_list = field_list_final.map((d, i) => asNumberColumn({ accessor: d, Header: fieldList_text[d], minWidth: 10, width: custom_maxWidth }, { color: colorScaleMap[d] }))

  const textFiled = (lang === "FRA") ? ["ENG_SENTENCE", "FRA_SENTENCE"] : ["ENG_SENTENCE", "KOR_SENTENCE"]
  const text_list = textFiled.map((d, i) => asTextColumn({ accessor: d, Header: d.toLowerCase().replaceAll("_"," "), width: 130 }, { format: showTextLabel }))

  // make category id the size of array

  //const categoriesArr = React.useMemo(() => Array(data.length).fill().map((d, i) => i))

  const idColumn = React.useMemo(() => asCategoricalColumn({ accessor: "id", Header: "ID" }, { categories: all_id_list }))

  const final_col_list = text_list.concat(idColumn).concat(stackNumColumn).concat(col_list)

  // col_list.push(stackNumColumn)
  const columns = React.useMemo(
    () => final_col_list,
    [state]
  );

  const initialState = React.useMemo(() =>
  (
    {
      "sortBy": [
        {
          id: "Weighted Sum",
          aesc: true
        }
      ],
      "hiddenColumns": [
        "id"
      ]
    }
  ), [])

  const fixed_column = ['expand', 'rank', 'selection', 'id', "ENG_SENTENCE", "FRA_SENTENCE", "Weighted Sum"]

  // Use the state and functions returned from useTable to build your UI
  var { getTableProps, getTableBodyProps, toggleRowSelected, toggleAllRowsSelected, setFilter, headerGroups, allColumns, rows, prepareRow, setColumnOrder, totalColumnsWidth, selectedFlatRows, state: { selectedRowIds, sortBy, filters } } = useTable(
    {
      columns,
      data,
      initialState,
      useControlledState: state => {
        return React.useMemo(
          () => {

            // const my_column_order = fixed_column.concat(php_lineup_state.column_order)


            const rev_id = lineup_filter_state.rev_filter_id
            if (rev_id.length == 0) {
              console.log("REV ID : " + rev_id)
              if (php_lineup_state.php_filter_map === null)
                return { ...state, filters: [] }
              else {
                const dict = php_lineup_state.php_filter_map;
                const my_filter = dict.map(d => { return { id: d.key, value: d.value } })

                return { ...state, filters: my_filter }
              }
            }
            else {
				var filter_list = [].concat(all_id_list)
				rev_id.forEach(id => {
					var filter_id_list = filter_state.rev_filter_info.filter(d => d.id === id)[0]
					console.log(filter_id_list)
					if(filter_id_list){
						filter_id_list = filter_id_list.filter_id
						filter_list = filter_list.filter(d => !filter_id_list.includes(d))
					}
				})
              return { ...state, filters: [{ id: "id", value: filter_list }] }
            }

            //set_filter_by_id_list(filter_id_list)

            if (php_lineup_state.php_filter_map === null)
              return { ...state, filters: [] }
            else {
              const dict = php_lineup_state.php_filter_map;
              const key = dict.key
              const value = dict.value
              return { ...state, filters: [{ id: key, value: value }] }
            }

          }

        ,[state,filter_state,lineup_filter_state,php_lineup_state])
      },
    },
    useFilters,
    useSortBy,
    useExpanded,
    useRowSelect,
    useRowSelectColumn,
    useRowRankColumn,
    useRowExpandColumn,
    useStats,
    useResizeColumns,
    featureFlexLayout,
    useBlockLayout,
    useColumnOrder
  );

  set_column_function = setColumnOrder

  // console.log(totalColumnsWidth)

  const sidePanel = () => {
    var summaryList = []
    var id_List = []
    headerGroups.map(headerGroup => {
      headerGroup.headers.map((column, i) => {
        if (i > 2) {
          if (column.Aggregated.name !== "TextSummaryRenderer") {
            // console.log(column)
            summaryList.push(column.render("Summary"))
            id_List.push(column.id)
          }
        }
        //console.log(column.render("Summary"))
      })
    })

    return (
      <div style={{ marginTop: 10 }}>
        {summaryList.map((summary, i) => {
          return <div>
            <h3> {id_List[i]} </h3>
            {summary}
          </div>

        })}

      </div>
    )
  }


  const rowSelectedEvent = (row) => {
    if (row.isSelected)
      toggleRowSelected(row.id, false)
    else
      toggleRowSelected(row.id, true)
  }


  const set_filter_by_id_list = (id_list) => {
    //const filter_id  = new (id_list)
    var filter_list = all_id_list.filter(d => !id_list.includes(d))
    setFilter("id", filter_list)
  }

  function rowMouseoverEvent(e, row) {
    dispatch(save_mouseover_id(row.id))
    dispatch(save_text_focus_id(row.rank))
    // console.log("Mouseover ID : " +row.id)
  }

  // Here, we need to update 
  // console.log(rows)
  // rows.map(d => d.id)


  const RenderRow = React.useCallback(
    ({ index, style }) => {
      const row = rows[index]
      prepareRow(row)
      return (
        <tr
          onClick={() => rowSelectedEvent(row)}
          onMouseOver={(e) => rowMouseoverEvent(e, row)}
          {...row.getRowProps({
            style,
          })}
        >
          {row.cells.map(cell => {
            return (
              <td {...cell.getCellProps()} >
                {cell.isGrouped
                  ? cell.render('Group')
                  : cell.isAggregated
                    ? // If the cell is aggregated, use the Aggregated
                    // renderer for cell
                    cell.render('Aggregated')
                    : // Otherwise, just render the regular cell
                    cell.render('Cell')}
              </td>




            )
          })}
        </tr>
      )
    },
    [prepareRow, rows, selectedRowIds]
  )

  const selectedIds = selectedFlatRows.map(d => d.original['id'])
  if (selectedIds.length === 0)
    selectedIds.push(0)
  //dispatch({type: "SET_TEXT_COMPARE_ID", data:selectedIds})

  // dispatch(save_text_compare_id(selectedIds))

  React.useEffect(() => {
    dispatch(save_text_compare_id(rows.map(d => d.id)))

  }, [php_lineup_state, state, sortBy, lineup_filter_state])


  // React.useEffect(() => {
  //   console.log(php_lineup_state.php_filter_map)
  //   if(php_lineup_state.php_filter_map == null)
  //     return
  //   const dict = php_lineup_state.php_filter_map;
  //   const key = dict.key
  //   const value = dict.value

  //   setFilter(key,value)
  // },[php_lineup_state])

  const saveFilter = () => {



    var filter_set = []

    

    selectedFlatRows.forEach(d => {
      filter_set.push(d.original['id'])
    })



    var infos = filter_state.rev_filter_info
    const length = infos.length

    const filter_ids = JSON.parse(JSON.stringify(filter_set))
    console.log(filter_set)
    // console.log(infos[length-1]['id'])
    var rev_id = 1
    if (length != 0)
      rev_id = infos[length - 1].id + 1
    const rev_color = select_color

    const currntWeight = JSON.parse(JSON.stringify(state.weight))

    const rev_filter_info = { name : filter_name, id: rev_id, rev_color: rev_color, filter_id: filter_ids, weight: currntWeight }

    infos.push(rev_filter_info)

	var not_selected_data= filter_state.not_selected_data_id
	not_selected_data= not_selected_data.filter(d => !rev_filter_info.filter_id.includes(d))

    dispatch(save_rev_filter_info({filter: infos, not_selected_data: not_selected_data}))
    // dispatch({ type: "SET_SAVE_FILTER_ADDED", data: (!state.isSavedFiltered) })
    setOpen(false)
	toggleAllRowsSelected(false)
	filter_num++;
	filter_name = "Label" + filter_num
    
  }

  function seeDetail() {
    const selectedIds = selectedFlatRows.map(d => d.original['id'])
    //console.log(seletedIds)
    const addr = "detail2/" + selectedIds
    window.open(addr, '_blank')

  }

  // const [filter_name, set_filter_name] = useState("Label")

  const handleFilterName = (e) => {
    filter_name = (e.target.value)
    console.log(e.target.value)
  }

  // Render the UI for your table
  return (
    <div>
		<div style={{height:70}}>
      <Title style={{float:"left"}}> Ranking View
      </Title>
	  {/* component="h5" variant="h6" color="black"  fontWeight="medium" gutterBottom */}
	  	{/* <Typography style={{ fontSize: "1em", float: "left" }} variant="body1" > */}
		<Typography style={{fontSize:"1em", float:"left"}} variant="body1" marginLeft="50px" marginTop="20px">
			# of Brushed Item : {rows.length}
        </Typography>
        <div style={{float:"right", marginTop: 20, marginRight:20}}>
        <Button color="inherit" variant="contained" size="small" onClick={() => {
// 'inherit' | 'primary' | 'secondary' | 'success' | 'error' | 'info' | 'warning',
            if(selectedFlatRows.length <=0)
            {
              alert("No item is Selected!")
              return
            }

          setOpen(true)
        }}>Save Ruleset</Button>
        </div>
	  </div>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >

        <div>
          {/* <Button variant="contained" onClick={handleOpen}>Open modal</Button> */}

          <Box sx={style}>
            <Typography variant="h6" component="div">
              Save Ruleset Infomation
            </Typography>
            <br />

            <div style={{ margin: 10 }}>

              <CirclePicker color={select_color} onChangeComplete={handleColorChange} />

            </div>
            <div style={{ marginTop: 35, marginLeft: 23 }}>
              <TextField
                required
                id="outlined-required"
                label={filter_name}
                defaultValue={filter_name}
                onChange={handleFilterName}
              />
            </div>

            <div style={{ textAlign: "right", marginTop: 30 }}>
              <Button variant="contained" onClick={saveFilter}>Save</Button>
            </div>
          </Box>
        </div>

      </Modal>


      <div id="myLineup" className="tableWrap" style={{marginTop: 20}}>
        <table {...getTableProps()} style={{margin:"auto"}} >
          <thead>
            {headerGroups.map((headerGroup) => (
              <tr {...headerGroup.getHeaderGroupProps()} >
                {headerGroup.headers.map((column, i) => (
                  <th style={{fontSize:"0.6em"}} {...column.getHeaderProps(column.getSortByToggleProps())}>
                    {column.render("Header")}
                    <span>
                      {i > 4 ? column.isSorted
                        ? column.isSortedDesc
                          ? ' '
                          : ' '
                        : '' : ''}
                    </span>
                    {column.hasOwnProperty("getResizerProps") ?
                      <div {...column.getResizerProps()}
                        className="resizer" /> : ""
                    }


                  </th>
                ))}
              </tr>
            ))}


            {headerGroups.map((headerGroup) => (
              <tr {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map((column, i) => (
                  <th {...column.getHeaderProps()}>
                    {i > 5 ? <WeightSlider header={column.id}/> : ''}
                  </th>
                ))}
              </tr>

            ))}
          </thead>
          <tbody {...getTableBodyProps()}>
            <FixedSizeList
              height={500}
              itemCount={rows.length}
              itemSize={35}
              width={totalColumnsWidth + scrollBarSize}
            >
              {RenderRow}
            </FixedSizeList>
          </tbody>
        </table>


      </div>
    </div>


  );
}

export default MyLineup;