

const initalState = {
    text_compare_id : [0],
  };
  
  export const TEXT_COMPARE_ID = "SET_TEXT_COMPARE_ID"
 
  export const save_text_compare_id = data => ({ type: TEXT_COMPARE_ID, data })

  
  const text_compare = (state = initalState, action) => {
    switch (action.type) {
        case "SET_TEXT_COMPARE_ID" :
            return {
              ...state,
              text_compare_id : action.data
        }
      // default    state count undefined  ! default 
      default:
        return state;
    }
  };
  
  export default text_compare