

const initalState = {
  rev_filter_id : []
};

export const LINEUP_FILTER_ID = "SET_LINEUP_FILTER_ID"


export const save_lineup_filter_id = data => ({ type: LINEUP_FILTER_ID, data })



const lineup_filter = (state = initalState, action) => {
  switch (action.type) {
    case "SET_LINEUP_FILTER_ID":
      return {
        ...state,
        rev_filter_id: action.data
      }

    // default    state count undefined  ! default 
    default:
      return state;
  }
};

export default lineup_filter