
const initalState = {
    mouseover_id : null,
  };
  
  export const MOUSEOVER_ID = "SET_MOUSEOVER_ID"
 
  export const save_mouseover_id = data => ({ type: MOUSEOVER_ID, data })

  
  
  const lineup_to_php = (state = initalState, action) => {
    switch (action.type) {
  
      case "SET_MOUSEOVER_ID":
        return {
          ...state,
          mouseover_id : action.data
        }

      // default    state count undefined  ! default 
      default:
        return state;
    }
  };
  
  export default lineup_to_php