
import {fieldList} from './StaticData'


const defaultWeight = {}
fieldList.forEach(d => {
  console.log(d)
  defaultWeight[d] = 0.5
})
const initalState = {
  weight : defaultWeight,
  isChangeWeight : false,
};

export const SAVE_WEIGHT = "SET_WEIGHT_VALUE"
export const SAVE_IS_WEIGHT = "SET_IS_CHANGE_WEIGHT"
export const save_weight_value = data => ({ type : SAVE_WEIGHT, data})
export const save_is_weight = data => ({ type : SAVE_IS_WEIGHT, data})


const weight = (state = initalState, action) => {
  switch (action.type) {
    case SAVE_WEIGHT:
      return {
        ...state,
        weight : action.data
      }
    case SAVE_IS_WEIGHT :
      return {
        ...state,
        isChangeWeight : action.data
      }

    // default    state count undefined  ! default 
    default:
      return state;
  }
};

export default weight