import React, { useEffect } from 'react'
import * as d3 from 'd3'
import { useSelector, useDispatch } from "react-redux";
import d3Tip from "d3-tip";

function FilterBundling() {
	const dispatch = useDispatch()
	const state = useSelector(state => state.filter)

	var diameter = 297,
	radius = diameter / 2,
	innerRadius = radius - 100; // between center and edge end

	useEffect(() => {
		
		var svg = d3.select("#filterbundling").append("svg")
			.attr("width", diameter)
			.attr("height", diameter)
			.append("g")
			.attr("transform", "translate(" + diameter / 2 + "," + diameter / 2 + ")");

		svg.append("g").attr("class", "link")
		svg.append("g").attr("class", "label")
		svg.append("g").attr("class", "bubble")

	}, [])

	useEffect(() => {
		if(state.rev_filter_info.length == 0) return;
		// Some constants controlling the graph appearance
		const PADDING_BUBBLE = 30 // distance between edge end and bubble
		const PADDING_LABEL = 40 // distance between edge end and engineer name
		const BUBBLE_SIZE_MIN = 6
		const BUBBLE_SIZE_MAX = 40
		const LINK_STROKE_MIN = 2
		const LINK_STROKE_MAX = 10

		// The 'cluster' function takes 1 argument as input. It also has methods (??) like cluster.separation(), cluster.size() and cluster.nodeSize()
		var cluster = d3.cluster()
			.size([360, innerRadius]);

		var line = d3.lineRadial()
			.curve(d3.curveBundle.beta(0.95))
			.radius(function (d) { return d.y; })
			.angle(function (d) { return d.x / 180 * Math.PI; });

		var svg = d3.select("#filterbundling").select("svg").select("g");

		svg.select("g.link").selectAll(".link").remove()
		svg.select("g.label").selectAll(".label").remove()
		svg.select("g.bubble").selectAll(".bubble").remove()
		
		var link = svg.select("g.link").selectAll(".link"),
			label = svg.select("g.label").selectAll(".label"),
			bubble = svg.select("g.bubble").selectAll(".bubble");
			
		// Add a scale for bubble size


		const data = state.rev_filter_info;

		// console.log(data);
		const hierarchicalData = data.map(d => {
			var bundle = {};
			bundle.id = "." + d.id;
			bundle.name = d.name;
			bundle.size = d.filter_id.length;
			bundle.filter_id = d.filter_id;
			bundle.color = d.rev_color;
			bundle.imports = [];
			data.forEach(d2 => {
				let intersection = d.filter_id.filter(x => d2.filter_id.includes(x));
				let count = intersection.length;
				if(count != 0 && d.id != d2.id)
					bundle.imports.push({"id": "." + d2.id, "count": count, "intersection" : intersection})
			});
			return bundle;
		});

			// If wanna see your data
			// console.log(hierarchicalData)

			// Reformat the data
			var root = packageHierarchy(hierarchicalData)
				//debugger;
				.sum(function (d) { return d.size; });
			// console.log(root)

			// Build an object that gives feature of each leaves
			cluster(root);
			let leaves = root.leaves()
			if(leaves.length == 1)
				leaves[0].x = 0
			// let angle_diff = leaves[0].x;
			// leaves.map(d => {
			// 	console.log(d)
			// 	d.x -= angle_diff;
			// 	return d;
			// })
			// console.log(leaves)
			var bubbleSizeScale = d3.scaleLinear()
				.domain([1, d3.max(leaves, d => d.value)])
				.range([BUBBLE_SIZE_MIN, BUBBLE_SIZE_MAX]);


			var linkStrokeScale = d3.scaleLinear()
			.domain([1, d3.max(packageImports(leaves), d => d.weight)])
			.range([LINK_STROKE_MIN, LINK_STROKE_MAX]);
			// console.log(packageImports(leaves));

			d3.selectAll(".tip-link").remove()
			var tip_link = d3Tip().attr('class', 'd3-tip tip-link')
				.offset([-20, 0])
				.html(function(event, d) {
					d3.select(event.target).attr("stroke", "red");
					tip_link.style("margin-left", (parseFloat((d3.select(".tip-link").style("left"))) - 960) / 4 + "px")
					tip_link.style("margin-top", parseFloat((d3.select(".tip-link").style("top"))) / 4 + "px")
					return 'Count : ' + d.weight;
				})

			svg.call(tip_link);

			// Leaves is an array of Objects. 1 item = one leaf. Provides x and y for leaf position in the svg. Also gives details about its parent.
			link = link
				.data(packageImports(leaves))
				.enter().append("path")
				.attr("class", "link")
				.attr("d", d => line(d.line))
				.attr("fill", "none")
				.attr("stroke", "black")
				.attr("stroke-width", d => linkStrokeScale(d.weight))
				.on('mouseover', tip_link.show)
				.on('mouseout', (event, d) => {
					tip_link.hide();
					d3.select(event.target).attr("stroke", "black");
				})
				.on('click', (event, d) => {
					
				})
				

			label = label
				.data(leaves)
				.enter().append("text")
				.attr("class", "label")
				.attr("dy", "0.31em")
				.attr("transform", function (d) { console.log(d);return "rotate(" + (d.x - 90) + ")translate(" + (d.y + PADDING_LABEL + bubbleSizeScale(d.value)) + ",0)" + (d.x < 90 || d.x > 270 ? "rotate(90)" : "rotate(-90)"); })
				.attr("text-anchor", "middle")
				.text(function (d) { return d.data.name; });

			d3.selectAll(".tip-bubble").remove()
			var tip_bubble = d3Tip().attr('class', 'd3-tip tip-bubble')
					.offset((e, d) => {return [Math.sin(d.x / 180 * Math.PI) * bubbleSizeScale(d.value) - bubbleSizeScale(d.value) - 20, Math.cos(d.x / 180 * Math.PI) * bubbleSizeScale(d.value)];})
					.html(function(event, d) {
						tip_bubble.style("margin-left", (parseFloat((d3.select(".tip-bubble").style("left"))) - 960) / 4 + "px")
						tip_bubble.style("margin-top", parseFloat((d3.select(".tip-bubble").style("top"))) / 4 + "px")
						return 'Count : ' + d.value;
					})

			svg.call(tip_bubble);

			bubble = bubble
				.data(leaves)
				.enter().append("circle")
				.attr("class", "bubble")
				.attr("transform", function (d) { return "rotate(" + (d.x - 90) + ")translate(" + (d.y + PADDING_BUBBLE) + ",0)" })
				.attr('r', d => bubbleSizeScale(d.value))
				.attr('stroke', 'black')
				.attr('fill', d => d.data.color)
				.style('opacity', .6)
				.on('mouseover', tip_bubble.show)
				.on('mouseout', tip_bubble.hide);

		// Lazily construct the package hierarchy from class names.
		function packageHierarchy(classes) {
			var map = {};

			function find(id, data) {
				var node = map[id], i;
				if (!node) {
					node = map[id] = data || { id: id, children: [] };
					if (id.length) {
						node.parent = find(id.substring(0, i = id.lastIndexOf(".")));
						node.parent.children.push(node);
						node.key = id.substring(i + 1);
					}
				}
				return node;
			}

			classes.forEach(function (d) {
				find(d.id, d);
			});

			return d3.hierarchy(map[""]);
		}

		// Return a list of imports for the given array of nodes.
		function packageImports(nodes) {
			var map = {},
				imports = [];

			// Compute a map from name to node.
			nodes.forEach(function (d) {
				map[d.data.id] = d;
			});

			// For each import, construct a link from the source to target node.
			nodes.forEach(function (d) {
				if (d.data.imports) d.data.imports.forEach(function (i) {
					imports.push({"line" : map[d.data.id].path(map[i.id]), "weight" : i.count, "intersection" : i.intersection});
				});
			});

			return imports;
		}
	}, [state])



	return (
		<div id="filterbundling"></div>
	)
}



export default FilterBundling;


