import p4 from 'p4.js'
import { forwardRef, useEffect, useImperativeHandle, useRef, Component } from 'react';
import { UserDispatch, globalData } from "./context/DataProvider";
import * as d3 from 'd3'
import 'd3-transition';
import { JsonToTable } from "react-json-to-table";
import { axisLeft, transition } from 'd3';



var new_data = globalData;

class Lines extends Component {
    state = {
      value: 100
    }
    handleChange = (e) => {
      this.setState({
        value: e.target.value
      })
      this.props.valueChanged(e.target.value);
    }
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.valueChanged(this.state.value);
      }

    render() {
      return (
        <form onSubmit={this.handleSubmit}>
            <label># lines</label>
          <input
        type = "number"
            min="10" 
            max={new_data.length}
            step="30"
            value={this.state.value}
            onChange={this.handleChange}
          />
        </form>
      );
    }
  }


const PCP = forwardRef((props, ref) => {

    const heatmap1_field_list = ["CORR", "BACK_ENG_CORR", "BACK_FRA_CORR", "LENGTH_DIFF", "TOKEN_LENGTH_DIFF", "SENTENCE_LEN_DIFF", "id"];
	
    


    var newTable = [];
    var nLines = 100;
    var x = null, y, dragging = {};
	var dimensions = heatmap1_field_list.filter(item => item != 'id');

    useImperativeHandle(ref, () => ({
        update(lineup, rank) { 
            new_data = [];
            if (lineup) {
                rank.forEach(d => {
                    new_data.push(d.original);
                })
            } else {
                new_data = globalData
            }
            updatePCP(new_data, nLines); 
        }
    }));

    const updatePCP = (new_data, nLines) => {

        var data = new_data.slice(0, nLines);

        const asc = arr => arr.sort((a, b) => a - b);

        const sum = arr => arr.reduce((a, b) => a + b, 0);

        const mean = arr => sum(arr) / arr.length;

        // sample standard deviation
        const std = (arr) => {
            const mu = mean(arr);
            const diffArr = arr.map(a => (a - mu) ** 2);
            return Math.sqrt(sum(diffArr) / (arr.length - 1));
        };

        const quantile = (arr, q) => {
            const sorted = asc(arr);
            const pos = (sorted.length - 1) * q;
            const base = Math.floor(pos);
            const rest = pos - base;
            if (sorted[base + 1] !== undefined) {
                return sorted[base] + rest * (sorted[base + 1] - sorted[base]);
            } else {
                return sorted[base];
            }
        };


        // 10   
        /* 
            1.  Data  0~10 value  .
            2. corr_quan[i] = between 0 ~ 9 ...   data  . // scaleLinear   
            3. Stroke Width  ... ? Count...    ?
            4.  Line   .. -> path    
            5.  Width        .
            5. d3.selectAll()
            4. corr_quan[i] -> 
        */




        const converted_data = data.map((d, i) => {
            var newD = {}
            heatmap1_field_list.forEach(key => {
                newD[key] = d[key];
            })
			newD["rank"] = i;
            return newD
        })

        //console.log(converted_data)

        var count = {}
        var count_id = {}
        converted_data.forEach(d => {
            const obj = JSON.parse(JSON.stringify(d))
            // console.log(obj)
            //delete d['id']
            var key = JSON.stringify(d)
            count[key] = (count[key] || 0) + 1

            if (key in count_id) {
                // console.log(key)
                // console.log(count_id)
                count_id[key].push(obj['id'])
            }
            else
                count_id[key] = [obj['id']]

        })

        //console.log(count_id)

        var counted_list = []
        Object.keys(count).forEach((key, i) => {
            var newJson = JSON.parse(key)
            newJson['count'] = count[key]
            //newJson['id'] = i
            newJson['id_list'] = count_id[key]
            counted_list.push(newJson)
        })

        // console.log(counted_list)

        data = counted_list



        const margin = { top: 50, right: 50, bottom: 10, left: 50 },
            width = 1600 - margin.left - margin.right,
            height = 500 - margin.top - margin.bottom;

        // append the svg object to the body of the page
        var svg = d3.select("svg#mysvg_PCP")

        svg
            .attr("width", width + margin.left + margin.right)
            .attr("height", height + margin.top + margin.bottom)

        svg = svg.select("g")
            .attr("transform",
                `translate(${margin.left},${margin.top})`);;
        // Parse the Data


        // Color scale: give me a specie name, I return a color
        const color = d3.scaleOrdinal()
            .domain(heatmap1_field_list)
            .range(d3.schemeCategory10)

        // Here I set the list of dimension manually to control the order of axis:
        

        if (x == null) {//update axis first time
            
            // For each dimension, I build a linear scale. I store all in a y object
            y = {}
            const thres = {}
            const n = [...new Array(10).keys()]

            var quan_scale = {}

            const widthExtent = d3.extent(data, d => d.count)
            const widthScale = d3.scaleLinear().domain(widthExtent).range([1, 40])

            var label = {}

            for (var i in dimensions) {
                var name = dimensions[i]
                const max = d3.max(globalData, d => +d[name])
                const min = d3.min(globalData, d => +d[name])
                y[name] = d3.scaleLinear()
                    //.domain([0, 1])
                    .domain([min, max]) // --> Same axis range for each group
                    // --> different axis range for each group --> .domain( [d3.extent(data, function(d) { return +d[name]; })] )
                    .range([height, 0])
                //label[name] = d3.scaleLinear().domain([0,numOfSplit]).range([min,max])
                // var quan = d3.scaleQuantize().domain([min,max]).range(n)
                // const tmp_scale = (val) => {
                //     return d3.scaleLinear().domain([9,0]).range([height,0])(quan(val))
                // }
                // quan_scale[name] = tmp_scale
                // console.log(quan_scale[name]((max)))
                //console.log(y[name](max))
                //console.log(thres[name]((min+max)/2))
            }

            // Build the X scale -> it find the best position for each Y axis
            x = d3.scalePoint()
                .range([0, width])
                .domain(dimensions);
		}



        // Highlight the specie that is hovered
        const highlight = function (event, d) {

            // var selected_specie = d.Species

            // first every group turns grey
            d3.selectAll(".line")
                .style("stroke", "lightgrey")
                .style("opacity", "0.2")
            // Second the hovered specie takes its color
            d3.selectAll(".d" + d.id)
                .style("stroke", "red")
                .style("opacity", "1")
                .style("zIndex", 100)

            //console.log(d.id_list)
            //const addr = "detail/" + d.id_list



            // var info_list = []
            // d.id_list.forEach(tmp_id => {
            //     const detail = data.filter(d => d.id === tmp_id)
            //     info_list.push(detail[0])
            // })
            // console.log(info_list)
            // newTable = info_list
            //console.log(d.id_list)
        }

        // Unhighlight
        const doNotHighlight = function (event, d) {
			console.log("asdf")
            d3.selectAll(".line")
                .style("stroke", function (d2, i) { return d3.interpolateRdBu(d2.rank/nLines) })
                .style("opacity", "0.2")
                .style("zIndex", 1)
        }

		function position(d) {
			
			var v = dragging[d];
			return v == null ? x(d) : v;
		  }

function transition(g) {
	return g.transition().duration(500);
  }
  
        // The path function take a row of the csv as input, and return x and y coordinates of the line to draw for this raw.
        function path(d) {
            return d3.line()(dimensions.map(
                function (p) {
                    // console.log(d[p])
                    // console.log(quan_scale[p](d[p]))
                    return [position(p), y[p](d[p])]; 
                }));
        }

        // Draw the lines
        console.log(new_data, data);
        var paths = svg.selectAll("path.line")
            .data(data, d => d ? d.id : -1);
        //console.log(paths);
        paths.enter()
            .append("path")
            .attr("class", function (d) { return "line d" + d.id }) // 2 class for each line: 'line' and the group name
            .attr("d", path)
            .style("fill", "none")
            //.style("stroke-width", function (d) { return widthScale(d.count) })
            .style("stroke-width", 1)
            .style("opacity", (d, i) => {
                return 0.2
            })
            .on("mouseover", highlight)
            .on("mouseleave", doNotHighlight)
            .on("dblclick", function (event, d) {

                // const ID = d.id
                const addr = "detail2/" + d.id_list
                window.open(addr, '_blank')
            });

            svg.selectAll("path.line").style("stroke", function (d, i) { return d3.interpolateRdBu(d.rank/nLines) })

        paths.exit().remove();
        // Draw the axis:

		var axis = svg.selectAll("g.dimension")
			.data(dimensions, d => d)
			.enter()
			.append("g")
			.attr("class", "dimension")
			// I translate this element to its right position on the x axis
			.attr("transform", function (d) { return `translate(${x(d)})` })
			//and add drag
			.call(d3.drag()
				//.subject(function(event, d) { return d == null ? {x: x(event.x)}: d; })
				.on("start", function(event, d) {
					dragging[d] = x(d);
				})
				.on("drag", function(event, d) {
					dragging[d] = Math.min(width, Math.max(0, event.x));
					dimensions.sort(function(a, b) { return position(a) - position(b); });
					x.domain(dimensions);
					axis.attr("transform", function(d) { return "translate(" + position(d) + ")"; })
				})
				.on("end", function(event, d) {
					delete dragging[d];
					transition(d3.select(this)).attr("transform", "translate(" + x(d) + ")");
					transition(d3.select("svg#mysvg_PCP").selectAll("path.line")).attr("d", path);
				}))



			// And I build the axis with the call function
			// Add axis title
		axis.append("g")
			.attr("class", "axis")
			.each(function(d) { d3.select(this).call(axisLeft(y[d])); })
			.append("text")
			.style("text-anchor", "middle")
			.attr("y", -9)
			.text(function (d) { return d; })
			.style("fill", "black")

		// console.log(svg.selectAll("path").data())
        


    }
    useEffect(() => {
        const numOfSplit = 10
        const splitArray = [...new Array(numOfSplit).keys()]
    
        var quantileScale = {}
        heatmap1_field_list.forEach((key) => {
            const tmp_extent = d3.extent(globalData, d => d[key])
            quantileScale[key] = d3.scaleQuantile().domain(tmp_extent).range(splitArray)
        })
    
        const sampled_data = globalData.map((d,i) => {
            var newD = {}
            heatmap1_field_list.forEach(key => {
                if(key === 'id')
                    newD[key] = d[key]
                else
                {
                    const myScale = quantileScale[key]
                    const newValue = myScale(d[key])
                    newD[key] = newValue
                }
            })
            return newD
        })
        var count = {}
        var count_id = {}
        sampled_data.forEach(d => {
            const obj = JSON.parse(JSON.stringify(d))
            delete d['id']
            var key = JSON.stringify(d)
            count[key] = (count[key] || 0 ) + 1
    
            if(key in count_id){
                count_id[key].push(obj['id'])
            }
            else
                count_id[key] = [obj['id']]
            
        })
    
        var sampled_list = []
        Object.keys(count).forEach((key, i) => { 
            const shuffled = count_id[key].sort(() => 0.5 - Math.random()).slice(0, Math.sqrt(count[key]));
            sampled_list.push(...shuffled);
        })
        console.log(sampled_list)
        new_data = [];
        globalData.forEach(d => {
            if(sampled_list.includes(d.id)){
                new_data.push(d);
            }
        })
        updatePCP(new_data, 100);
    });
      


      function valueChanged (d)  {
        nLines = (+d > globalData.length)? globalData.length: +d;
        updatePCP(new_data, nLines);
      }
    return (
    
    <div>
        <Lines valueChanged={valueChanged}/>
        <div id="my_dataviz">
            <svg id="mysvg_PCP">
				<g></g>
			</svg>

            <div>
                <JsonToTable json={newTable} />
            </div>
        </div>
    </div>
    )
});


export default PCP;