import { combineReducers } from "redux";
// import counter from "./counter";
import filter from "./filter";
import text_compare from "./text_compare";
import weight from "./weight";
import lineup_filter from "./lineup_filter";
import php_lineup from "./php_lineup";
import lineup_to_php from './lineup_to_php'
import mouseover_to_text from './mouseover_to_text'
//  reducer   reducer   .
// store    1.
const rootReducer = combineReducers({
  filter,
  text_compare,
  weight,
  lineup_filter,
  php_lineup,
  lineup_to_php,
  mouseover_to_text
});

export default rootReducer;