
import { globalData } from './StaticData'

const initalState = {
    rev_filter_info: [],
	not_selected_data_id : [...Array(globalData.length).keys()].map(i => i),
    filter_set: [],
	isSavedFiltered: false
}

export const REV_FILTER_INFO = "SET_REV_FILTER_INFO"
export const FILTER_LIST = "SET_FILTER_LIST"
export const IS_SAVED_FILTERED = "SET_IS_SAVED_FILTERED"

export const save_rev_filter_info = data => ({ type: REV_FILTER_INFO, data })
export const save_filter_list = data => ({ type: FILTER_LIST, data })
export const save_is_save_filtered = data => ({ type: IS_SAVED_FILTERED, data })


const filter = (state = initalState, action) => {
  switch (action.type) {
    case "SET_REV_FILTER_INFO":
      return {
        ...state,
        rev_filter_info: action.data.filter,
		not_selected_data_id : action.data.not_selected_data
      }
    case "SET_FILTER_LIST":
      return {
        ...state,
        filter_list: action.data
      }
    case "SET_IS_SAVED_FILTERED":
      return {
        ...state,
        isSavedFiltered: action.data
      }

    // default    state count undefined  ! default 
    default:
      return state;
  }
};

export default filter